class BlockA extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            rewards : 0
        }
        this.claim = this.claim.bind(this);
    }

    async claim(){
        // Modern Browsers like Chrome and Brave
        if (window.ethereum) {
            try {
                // Request account access
                await window.ethereum.enable();
            } catch (error) {
                // User denied account access...
                alert('You must connect your wallet !')
                return;
            }
        }else {
            // you cant connect
            console.error("Unable to connect to metamask");
            alert("You have to use Trustwallet app or install Metamask extension in your browser to use this app, you can install it from :  https://metamask.io/download.html");
            return;
        }
        let web3 = new Web3(window.ethereum);
        let accounts = await web3.eth.getAccounts();
        let account = accounts[0];
        let myContract = new web3.eth.Contract(contractAbi, stakingContractAddress);
        await myContract.methods.claim(account).send({
            from : account
        });
        window.location.reload();
    }

    async getRewards(){
        // Modern Browsers like Chrome and Brave
        if (window.ethereum) {
            try {
                // Request account access
                await window.ethereum.enable();
            } catch (error) {
                // User denied account access...
                alert('You must connect your wallet !')
                return;
            }
        }else {
            // you cant connect
            console.error("Unable to connect to metamask");
            alert("You have to use Trustwallet app or install Metamask extension in your browser to use this app, you can install it from :  https://metamask.io/download.html");
            return;
        }
        let web3 = new Web3(window.ethereum);
        let accounts = await web3.eth.getAccounts();
        let account = accounts[0];
        let myContract = new web3.eth.Contract(contractAbi, stakingContractAddress);
        let stats = await myContract.methods.stakingStats(account).call();
        this.setState({rewards: parseFloat(Web3.utils.fromWei(stats[1]))});
    }

    async componentDidMount(){
        await this.getRewards();
    }

    render() { 
        return ( 
        <div className="blocks doCenter col">
            <div className="heading">REWARDS</div>
            <img src={imgA} alt='' />
            <div>{this.state.rewards.toFixed(4)}</div>
            <div>HP</div>
            <div
            className="button"
            style={{ background: "#f8d252" }}
            onClick={this.claim}
            >
            CLAIM
            </div>
        </div>
        );
    }
}
 
export default BlockA;